import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../app/useRoot'
import Pacman from '../../common/Pacman'
import { mediaDesktop, mediaMobile } from '../../common/Responsive'
import {
  QFoodMenuWareCategories,
  useQFoodMenuCartPresence,
} from '../../graph/generated'
import FoodCategory from './FoodCategory'
import FoodMenuItem from './FoodMenuItem'
import { FoodInCart } from './types'

interface IProps {
  categories: ReadonlyArray<QFoodMenuWareCategories>
  isLoading: boolean
}

const FoodMenuStyled = styled.div`
  border-radius: 0 0 0.6rem 0.6rem;
  ${mediaDesktop`background: white;`};
  ${mediaMobile`
    background: #eee9e7;
    background-size: cover;
    background-attachment: scroll;
  `};
`

const FoodMenuListStyled = styled.div`
  ${mediaDesktop`
    margin: 0 1.4rem 1rem 1.6rem;
    padding: 2rem 0 2rem 0;
  `};
  ${mediaMobile`
    margin: 0 0.7rem 1rem 0.7rem;
    padding: 0 0 4rem 0;
  `};
`

export const FoodMenu = observer<IProps>(({ categories }) => {
  const { order: orderModel, config, user } = useRoot()
  const { data, loading } = useQFoodMenuCartPresence(
    {
      orderId: orderModel.orderId!,
    },
    {
      skip: !orderModel.branchId,
    },
  )

  const inCart: FoodInCart = loading
    ? {}
    : data.order.items.reduce((result, item) => {
        const recipeId = item.recipe.id
        if (!result[recipeId]) {
          result[recipeId] = 0
        }
        result[recipeId] += 1
        return result
      }, {})

  const canAddToCart =
    !loading && data.order.items.length < config.orderItemsMax

  const render = () => {
    if (loading) {
      return <Pacman noMargin={true} />
    }
    if (categories.length === 0) {
      return (
        <h3>
          <Trans>Food menu is empty. We are sorry.</Trans>
        </h3>
      )
    }
    return (
      <>
        {categories.map(category => (
          <FoodCategory key={category.id} category={category}>
            {category.recipes.map(recipe => (
              <FoodMenuItem
                key={recipe.id}
                recipe={recipe}
                inCart={inCart[recipe.id] || 0}
                isActive={orderModel.lastActiveRecipeId === recipe.id}
                canAddToCart={canAddToCart}
                onClick={() => {
                  if (canAddToCart) {
                    orderModel.setUsedRecipeId(recipe.id)
                  } else {
                    user.showOrderLimit()
                  }
                }}
                onPrefetch={() => {
                  if (recipe.sideDishCount > 0) {
                    orderModel.prefetchFood(recipe.id)
                  }
                }}
              />
            ))}
          </FoodCategory>
        ))}
      </>
    )
  }

  return (
    <FoodMenuStyled>
      <FoodMenuListStyled>{render()}</FoodMenuListStyled>
    </FoodMenuStyled>
  )
})
