import './CartView.css'

import React from 'react'

import { useQCartView } from '../../graph/generated'
import { AddToCartModal } from './AddToCartModal'
import { CartContents } from './CartContents'
import { CartIsEmpty } from './CartIsEmpty'
import { CartMinimalOrderPrice } from './CartMinimalOrderPrice'
import { ChangeCartModal } from './ChangeCartModal'
import { useCartRefresh } from './useCartRefresh'

interface IProps {
  orderId: MaybeID
}

export const CartView: React.FC<IProps> = ({ orderId }) => {
  const { data, loading, refetch } = useQCartView(
    { orderId: orderId! },
    {
      skip: !orderId,
    },
  )

  useCartRefresh(refetch)

  return (
    <>
      <AddToCartModal />
      <ChangeCartModal />

      {loading || data.order.countWares === 0 ? (
        <>
          <CartMinimalOrderPrice order={data.order} />
          <CartIsEmpty />
        </>
      ) : (
        <CartContents order={data.order} />
      )}
    </>
  )
}
