import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { useRoot } from '../../app/useRoot'
import { Mobile } from '../../common/Responsive'

interface IProps {
  recipeId: string
  canAddToCart: boolean
  hasSidedish: boolean
  onClick(): void
}

const LinkStyled = styled(Link)`
  color: inherit;
  &:visited div {
    border: inherit;
  }
`

export const LinkToCart: React.FC<IProps> = ({
  recipeId,
  hasSidedish,
  canAddToCart,
  onClick,
  children,
}) => {
  const { order } = useRoot()
  if (!canAddToCart) {
    return <div onClick={onClick}>{children}</div>
  }
  if (hasSidedish) {
    return (
      <LinkStyled to={{ pathname: `/order/add/${recipeId}` }} onClick={onClick}>
        {children}
      </LinkStyled>
    )
  }
  return (
    <Mobile>
      {isMobile => (
        <div
          onClick={() => {
            onClick()
            isMobile
              ? order.addFromRecipeMobile(recipeId)
              : order.addFromRecipe(recipeId)
          }}
        >
          {children}
        </div>
      )}
    </Mobile>
  )
}
