import { RM } from '../../helpers/ramda'

export interface ISidedishCategory {
  id: ID
  minCount: number
  maxCount: number
  sideDishes: ReadonlyArray<{
    id: ID
  }>
}

export function useSidedishValidator(
  categories: ReadonlyArray<ISidedishCategory>,
  selection: ReadonlyArray<ID>,
) {
  const sidedishesInCategory = categories.reduce(
    (result, category) => {
      result[category.id] = category.sideDishes.map(RM.prop('id'))
      return result
    },
    {} as Record<ID, ID[]>,
  )

  const categorySelection = (id: ID) =>
    RM.intersection(sidedishesInCategory[id], selection).length

  const canBeSelected = ({ maxCount, id }: ISidedishCategory) => {
    return maxCount === 0 || categorySelection(id) < maxCount
  }

  const categoryIsValid = ({ minCount, id }: ISidedishCategory) => {
    return minCount === 0 || categorySelection(id) >= minCount
  }

  return { canBeSelected, categoryIsValid }
}
