import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components'

import { QCartView } from '../../graph/types'

interface IProps {
  order: QCartView.Order
}

const MinimalOrderLabel = styled.div`
  color: ${({ theme }) => theme.secondary};
  margin: 1rem;
`

export const CartMinimalOrderPrice: React.FC<IProps> = ({ order }) => {
  return (
    <>
      {order && order.minimalOrderPrice && (
        <MinimalOrderLabel>
          <Trans>
            Minimal price of order is {order.minimalOrderPrice.formattedValue}
          </Trans>
        </MinimalOrderLabel>
      )}
    </>
  )
}
