import React from 'react'

import { PrimaryButton } from '../../common/Buttons'
import { GCartRecipeFragment } from '../../graph/generated'
import { i18n } from '../../i18n'
import { useSidedishValidator } from '../sidedishPicker/useSidedishValidator'

interface IProps {
  selection: ReadonlyArray<ID>
  recipe: GCartRecipeFragment
  onConfirm(): Promise<void>
}

export const CartOrderButton: React.FC<IProps> = ({
  selection,
  recipe,
  onConfirm,
}) => {
  const [isConfirming, setConfirming] = React.useState(false)
  const { categoryIsValid } = useSidedishValidator(recipe.categories, selection)

  return (
    <PrimaryButton
      className="modal__body__button"
      disabled={isConfirming || !recipe.categories.every(categoryIsValid)}
      data-testid="cartOrderButton"
      onClick={async () => {
        setConfirming(true)
        await onConfirm()
        setConfirming(false)
      }}
    >
      {i18n
        .plural({
          value: selection.length,
          0: 'Order without sidedish',
          one: 'Order with this sidedish',
          other: 'Order with # sidedishes',
        })
        .toLocaleUpperCase()}
    </PrimaryButton>
  )
}
