import '../../common/Modal.css'

import { Trans } from '@lingui/macro'
import React from 'react'
import { Container, Modal, ModalBody, ModalHeader } from 'reactstrap'

import Pacman from '../../common/Pacman'
import { GCartRecipe } from '../../graph/types'
import { RM } from '../../helpers/ramda'
import { SidedishPicker } from '../sidedishPicker/SidedishPicker'
import { CartOrderButton } from './CartOrderButton'

interface IProps {
  recipe: GCartRecipe.Fragment | undefined
  initialSelection?: ID[]
  isLoading: boolean
  onConfirm(selection: ID[]): Promise<void>
  onClose(): void
}

export const CartModal: React.FC<IProps> = ({
  recipe,
  initialSelection = [],
  onConfirm,
  onClose,
  isLoading,
}) => {
  const [selection, updateSelection] = React.useState(initialSelection)

  const onConfirmAndClose = async () => {
    await onConfirm(selection)
    onClose()
  }

  const onToggleSidedish = (sidedishId: ID) => {
    updateSelection(sel => {
      if (sel.includes(sidedishId)) {
        return RM.reject(RM.equals(sidedishId), sel)
      } else {
        return RM.append(sidedishId, sel)
      }
    })
  }

  return (
    <Modal
      isOpen={true}
      toggle={onClose}
      className="modal__container"
      autoFocus={true}
      backdrop="static"
      fade={false}
    >
      <ModalHeader toggle={onClose} tag={Container} className="modal__header">
        <span>
          <Trans>Do you want to add something?</Trans>
        </span>
      </ModalHeader>
      <ModalBody className="modal__body">
        {isLoading || !recipe ? (
          <div className="modal__body__content">
            <Pacman />
          </div>
        ) : (
          <>
            <div className="modal__body__content">
              <h3 className="modal__body__food__name">{recipe.nameLabel}</h3>
              <span className="modal__body__food_description">
                {recipe.descriptionLabel}
              </span>
              <SidedishPicker
                recipe={recipe}
                selection={selection}
                onToggle={onToggleSidedish}
              />
            </div>
            <div className="modal__body__button__line">
              <div className="modal__body__button__strip" />
              <CartOrderButton
                recipe={recipe}
                selection={selection}
                onConfirm={onConfirmAndClose}
              />
            </div>
          </>
        )}
      </ModalBody>
    </Modal>
  )
}
